<template>
  <div id="app">
    <div class="columns">
      <div v-for="(p, index) in players" v-bind:key="index" class="column is-3">
        <player :sips="p.sips" :name="p.name" :turn="is_turn(index)"/>
      </div>
    </div>
    <div clss="columns">
      <board lass="column is-12"
        :lines="lines"
        :revealed="revealed"
      />
    </div>
    <h1>
      {{revealed}}
    </h1>


    <!-- <input type="text" @change="add_points" v-model="revealed"/>
    <p>number of sips</p>
    <input type="radio" id="one" value="1" v-model="sips_selected">
    <label for="1">One</label>
    <br>
    <input type="radio" id="two" value="2" v-model="sips_selected">
    <label for="2">Two</label>
    <br>
    <input type="radio" id="three" value="3" v-model="sips_selected">
    <label for="3">Three</label>
    <br>
    <input type="radio" id="four" value="4" v-model="sips_selected">
    <label for="4">Four</label>
    <br>

    <button @click="change_turn"> change turn </button>
    <button @click="finish_word"> finnish word </button> -->
  </div>
</template>

<script>
import board from './components/board.vue'
import player from './components/player/player.vue'

export default {
  name: 'App',
  data() {
    return {
      lines: ["wie dit", "leest trekt", "een ad"],
      revealed: "",
      players: [{name: "henk", sips: 0}, {name: "piet", sips: 0}, {name: "sjaak", sips: 0}, {name: "jens", sips: 0}],
      turn: 0,
      sips_selected: 1,
      connection: null,
    }
  },
  components: {
    board,
    player
  },
  methods: {
    is_turn(n) {
      return n == this.turn
    },
    add_points() {
      var c = this.revealed.slice(-1);
      if (!"aeoui ".includes(c)){
        this.players[this.turn].sips += ((this.lines.join('')).split(c).length - 1) * this.sips_selected
      } else {
        this.players[this.turn].sips -= 4
      }
    },
    finish_word() {
      this.players[this.turn].sips += 16
    },
    change_turn() {
      this.turn = (this.turn + 1) % this.players.length
    }
  },
  created() {
    console.log("starting connection to websocket server")
    this.connection = new WebSocket("ws://dipsomania.bramvandartel.nl:80")

    this.connection.onmessage = (event) => {
      console.log(event.data)
      var message = event.data
      var msplit = message.split(";")
      if (message == "t"){
        this.change_turn()
        return
      }

      //guess letter
      if (msplit[0] == "gl"){
        this.sips_selected = msplit[1]
        if (!this.revealed.includes(msplit[2])){
          this.revealed = this.revealed + msplit[2]
          this.add_points()
        }
        return
      }

      //set word
      if (msplit[0] == "sw") {
        this.lines = msplit.slice(1)
        this.revealed = ""
        return
      }

      //reset
      if (msplit[0] == "r") {
        this.players.forEach(player => {
          player.sips = 0
        });
        this.revealed = ""
        return
      }

      //change player name
      if (msplit[0] == "cn") {
        this.players[msplit[1]].name = msplit[2]
        return
      }

      //finish word
      if (msplit[0] == "fw") {
        this.revealed = "abcdefghijklmnopqrstuvwxyz"
        this.finish_word()
        return
      }

      //add sips, in case of fuckups or hilarity
      if (msplit[0] == "as"){
        this.players[msplit[1]].sips += Number(msplit[2], 10)
      }
    }

    this.connection.onopen = function(event) {
      console.log(event)
      console.log("successfully connected")
    }
  }
}
</script>

